<template>
    <div>
        <div class="container">
            <div>
                <h2>Регистрация</h2>
                <p>Добавить проект</p>
            </div>
            <hr class="mb-5">
            <div class="row">
                <div class="col-md-8">
                    <b-form @submit="onSubmit" @reset="onReset">
                        <div class="row">
                            <div class="col-md-6">
                                <b-form-group label="Ваше имя:">
                                    <b-form-input v-model="form.name" type="text" required placeholder="Введите имя"></b-form-input>
                                </b-form-group>
                            </div>
                            <div class="col-md-6">
                                <b-form-group label="Ваш email:">
                                    <b-form-input v-model="form.email" type="email" required placeholder="Введите email"></b-form-input>
                                </b-form-group>
                            </div>
                            <div class="col-md-6">
                                <b-form-group label="Пароль:">
                                    <b-form-input v-model="form.password" type="password" required placeholder="Введите пароль"></b-form-input>
                                </b-form-group>
                            </div>
                            <div class="col-md-6">
                                <b-form-group label="Повторите пароль:">
                                    <b-form-input v-model="form.passwordRepeat" type="password" required placeholder="Введите пароль"></b-form-input>
                                </b-form-group>
                            </div>
                            <div class="col-md-12">
                                <b-alert :show="dismissCountDown" dismissible variant="danger" @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
                                    <p>{{ error }}</p>
                                    <b-progress variant="danger" :max="dismissSecs" :value="dismissCountDown" height="4px"></b-progress>
                                </b-alert>
                            </div>
                            <div class="col-md-8 mt-3">
                                <b-button type="submit" variant="primary" class="mr-2">Отправить</b-button>
                                <b-button type="reset" variant="danger">Сбросить</b-button>
                            </div>
                        </div>
                    </b-form>
                </div>
            </div>
            <hr class="py-5">
        </div>

        <b-modal ref="successModal" centered hide-footer title="Сообщение" @hide="onCloseModal">
            <div class="d-block">
                <h3>Пользователь зарегистрирован</h3>
            </div>
        </b-modal>
    </div>
</template>

<script>
    import { mapActions, mapState } from 'vuex';

    export default {
        name: "Registration",
        data() {
            return {
                dismissSecs: 5,
                dismissCountDown: 0,
                showDismissibleAlert: false,
                error: '',
                form: {
                    name: '',
                    email: '',
                    password: '',
                    passwordRepeat: '',
                },
            }
        },
        computed: {
            ...mapState({
                'getParams': state => state.params
            })
        },
        methods: {
            ...mapActions([
                'loadParams',
                'toggleLoad',
                'addUser'
            ]),
            countDownChanged (dismissCountDown) {
                this.dismissCountDown = dismissCountDown;
            },
            showAlert(error) {
                this.error = error;
                this.dismissCountDown = this.dismissSecs;
            },
            onCloseModal() {
                this.$router.push('login');
            },
            onSubmit(e) {
                e.preventDefault();

                if (this.form.password === this.form.passwordRepeat) {
                    this.toggleLoad(true);

                    this.addUser(this.form).then(() => {
                        this.$refs.successModal.show();
                    }).catch(error => {
                        this.showAlert(error.response.data.error);
                    }).finally(() => {
                        this.toggleLoad(false);
                    });
                } else {
                    this.showAlert('Пароли не совпадают');
                }
            },
            onReset(e) {
                e.preventDefault();

                this.form.name = '';
                this.form.email = '';
                this.form.password = '';
                this.form.passwordRepeat = '';
            }
        }
    }
</script>